.card-language {
    box-shadow: 0 4px 8px 0 rgba(111,1,222,0.2);
    transition: 0.3s;
    width: 100%;
}

.card-language:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card-language img {
    padding: 5px;
    width: 200px;
    height: 200px;
    max-width: 100%;
}

.card-info {
    padding: 10px 16px;
}

.card-name {
    font-weight: bold;
    display: block;
    font-size: x-large;
}

.card-description {
    padding-top: 10px;
    display: block;
}

.card-used li{
    list-style-type: none;
}